import {action, observable} from 'mobx';
import {StoreBase} from "../utils/StoreBase";

export class AccountStore extends StoreBase {

    @observable
    dataVersion = `${Date.now()}`;

    @observable
    account = ''; // 账号

    @observable
    password = ''; // 密码

    @observable
    gas = 3550000; // Gas

    @observable
    tokenBalance = null; // TOKEN余额

    @observable
    ttBalance = 0; // ETC余额

    @observable
    betAddress = ''; // 游戏合约地址

    @observable
    locale = 'en'; // 账号语言

    @action
    updateDataVersion() {
        this.dataVersion = `${Date.now()}`;
    }

}

export const accountStore = new AccountStore();
export default accountStore;
