import {action, observable} from "mobx";
import {GameUIScene, PopupNames} from "../utils/constants";


export class GameUIStore {

    @observable
    public scene = GameUIScene.moneySelector;

    @observable
    public visiblePopup = PopupNames.none;

    @observable
    public popupData: any = null;

    @observable
    private sceneData: any = null;

    @action
    public setScene(scene: GameUIScene, sceneData: any = null) {
        this.sceneData = sceneData;
        this.scene = scene;
    }

    @action
    public setVisiblePopup(popupName: PopupNames, popupData: any = null) {
        this.popupData = popupData;
        this.visiblePopup = popupName;
    }

}

export const gameUIStore = new GameUIStore();
