import {action, has, observable} from 'mobx';
import _ from 'lodash';

export class StoreBase {

    @observable
    initialTimes = 0;

    @action
    init = (values: object) => {
        this.initialTimes = this.initialTimes + 1;
        _.forEach(values, (value, key) => {
            if (has(this, key)) {
                // @ts-ignore
                this[key] = value;
            }
        });
    };

}
