import React, {useEffect, useState} from 'react';
import {getCommonAPI} from '@web3game/apis/common/getCommonAPI';
import {hot} from 'react-hot-loader';
import {Provider} from "mobx-react";
import {Button, Modal, Navigator, Page} from 'react-onsenui';
import * as stores from '@web3game/common/stores';
import {PageLoading} from "@web3game/common/components/PageLoading";
import {compose} from "@web3game/common/utils/compose";
import {getLang} from "@web3game/lang";
import {HomePageAsync} from "./routes/asyncComponents";
import './styles';
import {Block} from "./components/common/Block";

// @ts-ignore
const renderPage = (route, navigator) => (
    <route.component key={route.key} navigator={navigator} />
);

export const MobileApplication = () => {
    const { accountStore } = stores;
    const [web3, setWeb3] = useState(null);
    const [web3ErrorMessage, setWeb3ErrorMessage] = useState('');
    const [web3TryTimes, setWeb3TryTimes] = useState(0);
    const retryGetWeb3 = () => {
        setWeb3TryTimes(web3TryTimes + 1);
        setWeb3ErrorMessage('');
    };

    // Auto Reload Page when account is changed
    useEffect(() => {
        const intervalId = setInterval(async () => {
            try {
                const commonAPI = await getCommonAPI();
                const [account] = await commonAPI.getAccount();
                if (
                    accountStore.account
                    && accountStore.initialTimes > 0
                    && account !== accountStore.account
                ) {
                    document.location.reload();
                }
            } catch (e) {
                console.error(e);
            }
        }, 1000);

        return () => clearInterval(intervalId);
    }, []);

    useEffect(() => {
        (async () => {
            try {
                const commonAPI = await getCommonAPI();
                const wb3 = await commonAPI.getWeb3();

                const [account] = await commonAPI.getAccount();
                const tokenBalance = await commonAPI.getTokenBalance();
                const ttBalance = await commonAPI.getTTBalance();
                accountStore.init({
                    account,
                    tokenBalance,
                    ttBalance,
                });
                setWeb3(wb3);
            } catch (error) {
                const errorMessage = error instanceof Error ? error.message : error;
                setWeb3ErrorMessage(errorMessage);
            }

        })();
    }, [web3TryTimes]);

    if (web3ErrorMessage) {
        return (
            <Page>
                <Modal isOpen>
                    <Block transparent style={{wordBreak: "break-word"}}>
                        {web3ErrorMessage}
                        <br />
                        <br />
                        <Button onClick={retryGetWeb3}>
                            {getLang().RETRY_GET_WEB3_NOW}
                        </Button>
                    </Block>
                </Modal>
            </Page>
        );
    }

    if (!web3 || !accountStore.account) {
        return <PageLoading />;
    }

    return (
        <Provider {...stores} web3={web3}>
            <Navigator
                renderPage={renderPage}
                initialRoute={{ component: HomePageAsync, key: 'MAIN_PAGE' }}
            />
        </Provider>
    );
};

export default compose(
    hot(module),
)(MobileApplication);
