import React from "react";

import "../styles/pageLoading.scss";
import {Page} from "react-onsenui";

export function PageLoading() {
    return (
        <Page key="PageLoading">
            <div
                className="bubblingG"
                style={{
                    height: '100%',
                    display: 'flex',
                    alignItems: 'center',
                }}
            >
                <span id="bubblingG_1" />
                <span id="bubblingG_2" />
                <span id="bubblingG_3" />
            </div>
        </Page>
    );
}
