import React, {CSSProperties} from 'react';
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import classNames from 'classnames';

export type BlockProps = {
    children?: React.ReactElement | React.ReactElement[] | string | number | any;
    style?: CSSProperties;

    className?: string;

    fullWidth?: boolean;
    fullHeight?: boolean;

    halfOffset?: boolean;
    noOffset?: boolean;
    transparent?: boolean;
    textLeft?: boolean;
    textRight?: boolean;
    textCenter?: boolean;
    withBoxShadow?: boolean;

    noPadding?: boolean;
    noMargin?: boolean;

    halfMarginTop?: boolean;
    oneMarginTop?: boolean;
    doubleMarginTop?: boolean;

    halfMarginBottom?: boolean;
    oneMarginBottom?: boolean;
    doubleMarginBottom?: boolean;

    halfHorizontalMargin?: boolean;
    oneHorizontalMargin?: boolean;
    oneVerticalMargin?: boolean;
    halfVerticalMargin?: boolean;
    doubleHorizontalMargin?: boolean;
    doubleVerticalMargin?: boolean;

    halfBottomPadding?: boolean;
    oneBottomPadding?: boolean;
    doubleBottomPadding?: boolean;

    halfTopPadding?: boolean;
    oneTopPadding?: boolean;
    doubleTopPadding?: boolean;

    halfHorizontalPadding?: boolean;
    oneHorizontalPadding?: boolean;
    doubleHorizontalPadding?: boolean;

    halfVerticalPadding?: boolean;
    oneVerticalPadding?: boolean;
    doubleVerticalPadding?: boolean;
};

export const BlockPure = (
    {
        children,
        className = '',
        style,
        ...styleProps
    }: BlockProps
) => {

    const clsName = classNames(
        'block',
        styleProps,
        className
    );

    return (
        <div className={clsName} style={style}>
            {children}
        </div>
    );
};

export const Block = compose<BlockProps>(
    hot(module),
    React.memo,
)(BlockPure);
